@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1080px !important;
  }
  .container.c-blog,
  .container-lg.c-blog,
  .container-md.c-blog,
  .container-sm.c-blog,
  .container-xl.c-blog,
  .container-xxl.c-blog {
    max-width: 900px !important;
  }
}
@media (min-width: 1400px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1200px !important;
  }
  .container.c-blog,
  .container-lg.c-blog,
  .container-md.c-blog,
  .container-sm.c-blog,
  .container-xl.c-blog,
  .container-xxl.c-blog {
    max-width: 1000px !important;
  }
}
#footer {
  background-color: #404040;
  color: #fff;
  font-size: 14px;
  padding: 10px 0 20px 0;
}
#footer .footer-widgets {
  margin-top: 30px;
}
#footer .footer-widgets .row-links {
  border-bottom: 1px solid #606060;
  padding-bottom: 20px;
  margin-bottom: 20px;
}
#footer .footer-widgets .row-links .logo {
  width: 150px;
  border-right: 1px solid #606060;
  margin-right: 10px;
}
#footer .footer-widgets .row-links nav {
  display: flex;
  align-items: center;
  width: max-content;
}
#footer .footer-widgets .row-links nav ul {
  display: flex;
  gap: 50px;
  padding: 0;
  margin-bottom: 0;
}
#footer .footer-widgets .row-links nav ul li {
  list-style: none;
}
#footer .footer-widgets .row-links nav ul li a {
  font-size: 14px;
  text-decoration: none;
  color: #fff;
  transition: all ease 0.3s;
}
#footer .footer-widgets .row-links nav ul li a:hover {
  color: #ec0001;
}
#footer .footer-widgets .row-links nav ul li:nth-last-child(1) a {
  border: 1px solid #fff;
  padding: 5px 20px;
  border-radius: 20px;
}
#footer .footer-widgets .row-links nav ul li:nth-last-child(1) a:hover {
  background-color: #fff;
  color: #404040;
}
#footer .footer-widgets .row-infos h4 {
  font-size: 14px;
  color: #fff;
  margin-bottom: 10px;
}
#footer .footer-widgets .row-infos a {
  display: flex;
  align-items: center;
  text-decoration: none;
  font-size: 14px;
  color: #fff !important;
  margin: 8px 0;
  transition: all ease 0.3s;
}
#footer .footer-widgets .row-infos a span {
  color: #fff;
  transition: all ease 0.3s;
}
#footer .footer-widgets .row-infos a i {
  color: #fff;
  font-size: 20px;
  margin-right: 8px;
  transition: all ease 0.3s;
}
#footer .footer-widgets .row-infos a:hover {
  color: #ec0001 !important;
}
#footer .footer-widgets .row-infos a:hover span,
#footer .footer-widgets .row-infos a:hover i {
  color: #ec0001 !important;
}
#footer .footer-widgets .row-infos .social-icons {
  display: flex;
  gap: 15px;
  padding: 0;
}
#footer .footer-widgets .row-infos .social-icons li {
  list-style: none;
}
#footer .footer-widgets .row-infos .social-icons li i {
  font-size: 26px;
}
#footer .footer-bottom {
  text-align: center;
  font-size: 12px;
  margin-top: 40px;
}
#footer .footer-bottom p {
  color: #cecece;
  margin-bottom: 0;
}